import indexList from "../../components/index/index-list.vue";
import swiperTabHead from "../../components/index/swiper-tab-head.vue";
import loadMore from "../../components/common/load-more.vue";
import noThing from "../../components/common/no-thing.vue";
import {mapState, mapMutations} from "vuex"
export default {
	components: {
		indexList,
		swiperTabHead,
		loadMore,
		noThing
	},
	data() {
		return {
			swiperheight: 500,
			tabIndex: 1,
			shoNo:false,
			tabBars: [
				{
					name: "推荐",
					id: "tuijian",
					page: 1
				},
				{
					name: "韩府",
					id: "hanfu",
					page: 1
				},
				{
					name: "娱乐",
					id: "yule",
					page: 1
				},
				{
					name: "二手",
					id: "ershou",
					page: 1
				},
				{
					name: "周边",
					id: "zhoubian",
					page: 1
				},
			],
			newslist: [
				{
					loadtext: "上拉加载更多",
					id: "tuijian",
					list: [
					]
				},
				{
					loadtext: "上拉加载更多",
					id: "hanfu",
					list: [
					]
				},
				{
					loadtext: "上拉加载更多",
					id: "yule",
					list: []
				},
				{
					loadtext: "上拉加载更多",
					id: "ershou",
					list: []
				},
				{
					loadtext: "上拉加载更多",
					id: "zhoubian",
					list: []
				}
			],


		}
	},
	 onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100)
				this.swiperheight = height;
			}
		});
		 this.requestData()
		
	},
	onShow() {
		// this.requestData()
	},
	// 监听搜索框点击事件
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search',
		});
	},
	// 监听原生标题导航按钮点击事件
	onNavigationBarButtonTap(e) {
		switch (e.index) {
			case 1:
				// 打开发布页面
				uni.navigateTo({
					url: '../add-input/add-input',
				});
			case 0:
				uni.showToast({
					title: '签到成功',
				});

				break;
		}
	},
	computed:{
	...mapState(['userInfo'])
	},
	methods: {
		async requestData(GoPage,Gotype){
			let currentPage = GoPage || this.tabBars[this.tabIndex].page;
			let type = Gotype || this.tabBars[this.tabIndex].id;
			let data;
			try{

				data = await this.$http.get(`/topic/page?page=${currentPage}&type=${type}&rows=10`)
			}catch(e){
				console.log(e)
				return 
			}
			let {page,items} = data
			if(items&&items.length ===0){
				this.tabBars[this.tabIndex].page=page
				this.newslist[this.tabIndex].loadtext="没有更多数据了";
				return 
			}
			items.forEach((item)=>{
				if(item.images!=''){
					item.images = item.images.split(",");
				}else{
					item.images =[]
				}
				
			})
			this.tabBars[this.tabIndex].page=page
			this.newslist[this.tabIndex].list=this.newslist[this.tabIndex].list.concat(items)
			if(items&&items.length <10){
				this.newslist[this.tabIndex].loadtext="没有更多数据了";
			}
		},
		async likeOrTread(data){
			if(!this.userInfo || !this.userInfo.id){
				uni.showToast({
					title:"你还未登录！或登录过有效期!",
					icon:'none'
				})
				return
			}
			await this.$http.post('topic/active',data);
		},
		checkIn() {
			console.log("checkIn")
			uni.showToast({
				title: '签到成功',
			});
		},
		// 上拉加载
		publish() {
			console.log("publish")
			// 打开发布页面
			uni.navigateTo({
				url: '../add-input/add-input',
			})
		},
		searchInfo() {
			console.log("searchInfo")
			uni.navigateTo({
				url: '../search/search',
			});
		},

		loadmore(index) {
			if (this.newslist[index].loadtext != "上拉加载更多") {
				return;
			}
			// 修改状态
			this.newslist[index].loadtext = "加载中...";
			// 获取数据
			this.requestData(this.tabBars[this.tabIndex].page+1)
			// this.newslist[index].loadtext = "";
			
			
		},
		// tabbar点击事件
		tabtap(index) {
			this.tabIndex = index;
		},
		// 滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
			this.requestData(this.tabBars[this.tabIndex].page,this.tabBars[this.tabIndex].id)
		}
	},
}
